<template>
  <user>
    <html-head 
      title="Product Categories"
    ></html-head>

    <page-header>
      <template v-slot:title>Product Variation Settings</template>
    </page-header>

    <v-row>
      <v-col cols="12" lg="6">
        <variation-options-form 
          v-if="!resources.isFetching"
          type="variation" 
          @submitted="getResources"
          v-model="edit"
        ></variation-options-form>
      </v-col>
      <v-col cols="12" lg="6">
        <template v-if="resources">
          <variation-crud-card 
            v-if="!resources.isFetching"
            :api="api.category"
            :changed="getResources"
            @onClickEdit="addValueToEdit"
            :resources="resources"
          ></variation-crud-card>
        </template>
      </v-col>
    </v-row>
  </user>
</template>

<script>
import Api from '../../routes/api'
import Resources from '@/core/models/Resources'

export default {
  data: () => ({
    api: Api,
    headers: [
      {
        text: 'Name',
        align: 'start',
        value: 'name',
      },
      {
        text: 'Created',
        align: 'start',
        value: 'createdAt',
      },
      {
        text: 'Actions',
        align: 'end',
        value: 'actions',
      },
    ],

    resources: new Resources,
    edit: {}
  }),

  methods: {
    getResources () {
      this.resources.getResources({
        method: 'get',
        url: Api.category.getGroupByParent(),
        params: {
          q: {
            type: 'variation'
          }
        }
      } )
    },

    addValueToEdit ( value ) {
      this.edit = value
    }
  },

  mounted () {
    this.getResources()
    this.resources.load( false )
  }
}
</script>